.page-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: $font-size-desktop-s;
  font-weight: $font-weight-regular;
  display: flex;
}

@media (max-width: $mobile-full-width) {
  .page-navigation__list {
    font-size: $font-size-mobile-m;
    flex-direction: column;
  }
}
